import { Component, OnInit } from '@angular/core';
import { IProduct } from '../../shared/interfaces/product.interfaces';
import { OrderService } from '../../shared/services/order.service';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss'],
})
export class ProductsComponent implements OnInit {
  products: IProduct[] = [
    {
      id: 1,
      name: 'article 1',
      label:
        'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nobis modi reprehenderit beatae esse ratione aliasea, dignissimos culpa enim non voluptas blanditiis nam impedit animi!',
      price: 25,
      count: 1,
    },

    {
      id: 2,
      name: 'article 2',
      label:
        'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nobis modi reprehenderit beatae esse ratione aliasea, dignissimos culpa enim non voluptas blanditiis nam impedit animi!',
      price: 35,
      count: 1,
    },

    {
      id: 3,
      name: 'article 3',
      label:
        'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nobis modi reprehenderit beatae esse ratione aliasea, dignissimos culpa enim non voluptas blanditiis nam impedit animi!',
      price: 45,
      count: 1,
    },
  ];

  constructor(
    private orderService: OrderService
  ) { }

  ngOnInit(): void { }

  public productCount(product: IProduct, status: boolean): void {
    if (status) {
      product.count++;
    } else {
      if (product.count > 1) {
        product.count--;
      }
    }
  }

  public addBasket(product: IProduct): void {
    let localProducts: Array<IProduct> = [];

    if (localStorage.length > 0 && localStorage.getItem('products')){
      localProducts = JSON.parse(localStorage.getItem('products'));
      if(localProducts.some(prod => prod.id === product.id)){
        const index = localProducts.findIndex(prod => prod.id === product.id);
        localProducts[index].count += product.count;
      }else{
        localProducts.push(product);
      }
    } else {
      localProducts.push(product);
    }
    localStorage.setItem('products', JSON.stringify(localProducts));
    product.count = 1;
    this.orderService.basket.next(localProducts);
  }
}
