import { Component, OnInit, TemplateRef } from '@angular/core';
import { IProduct } from 'src/app/shared/interfaces/product.interfaces';
import { OrderService } from 'src/app/shared/services/order.service';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-basket',
  templateUrl: './basket.component.html',
  styleUrls: ['./basket.component.scss']
})
export class BasketComponent implements OnInit {

  basket: Array<IProduct> = [];
  totalPrice = 0;

  displayedColumns: string[] = ['id', 'name', 'label', 'price', 'count', 'totalPrice'];

  constructor(
    private orderService: OrderService,
    public dialog: MatDialog
  ) { }

  ngOnInit(): void {
    this.getLocalStorage();
    this.checkBasket();
  }

  private getLocalStorage(): void {
    if (localStorage.length > 0 && localStorage.getItem('products')) {
      this.basket = JSON.parse(localStorage.getItem('products'));
      this.totalPrice = this.basket.reduce((total, product) => total + (product.price * product.count), 0);
    }
  }

  private checkBasket(): void {
    this.orderService.basket.subscribe(
      data => {
        this.getLocalStorage();
      });
  }

  private clearOrder(): void {
    this.basket = [];
    localStorage.setItem('products', JSON.stringify(this.basket));
    this.orderService.basket.next(this.basket);
  }

  public openDialog(template: TemplateRef<any>, status: boolean): void {
    const dialogRef = this.dialog.open(template);
    if (status) {
      this.clearOrder();
    }
  }
}
